.home {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
    }
  .home_image{
    width: 100%;
    z-index: -1;
    margin-bottom: -200px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  }
  .home_row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
  }

  /* Media query */

  @media screen and (max-width:768px) {
    .home_row{
      flex-direction: column;
      align-items: center;
    }
    .home_image{
      margin-bottom: 0px;
    }
  }